body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  box-sizing: border-box;
}

code {
  font-family: Consolas, "Courier New", monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

li {
  list-style: none;
}
html {
  width: 100vw;
  height: 100vh;
  background-image: -webkit-linear-gradient(315deg, #649cf0 0%, #1d62d1 100%);
  background-image: linear-gradient(135deg, #649cf0 0%, #1d62d1 100%);
  min-width: 320px;
  min-height: 320px;
}
body {
  background: url(/static/media/bg.bbb14f07.png) no-repeat top center;
  background-size: auto 50%;
  height: 100%;
  font-size: 1.1rem;
  font-family: Lato;
  display: flex;
  align-items: center;
  justify-content: center;
}

#main {
  color: white;
  width: 35vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  /* margin-top: 20vh; */
  zoom: 170%;
}

#main h1 {
  text-decoration: underline;
}
#main h1,
h2 {
  text-align: center;
  font-size: 1.2rem;
  padding: 1rem;
}

.card li {
  margin: 2rem 0;
}

.select {
  width: 100%;
  color: black;
  /* margin-top: 2vh; */
}

.select select {
  width: 100%;
  padding: 0.8rem;
  border-radius: 2px;
}

.name {
  text-transform: capitalize;
}
.time {
  float: right;
}

.difference {
  float: right;
  margin-right: 20px;
  color: red !important;
}

.next-prayer {
  font-weight: 700;
  background-color: white;
  color: black;
  padding: 0.5rem 0.4rem 0.5rem;
  border-radius: 2px;
}

.spinner_spinner__13LQm {
  /* display: inline-block; */
  width: 64px;
  height: 64px;
  flex: 1 1;
}

.spinner_container__3X5jo {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.spinner_row__297t4 {
  flex: 1 1;
}
.spinner_spinner__13LQm:after {
  content: ' ';
  display: block;
  width: 46px;
  height: 46px;
  margin: 1px;
  border-radius: 50%;
  border: 5px solid #fff;
  border-color: #fff transparent #fff transparent;
  -webkit-animation: spinner_spinner__13LQm 1.2s linear infinite;
          animation: spinner_spinner__13LQm 1.2s linear infinite;
}
@-webkit-keyframes spinner_spinner__13LQm {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes spinner_spinner__13LQm {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

